<template>
  <div class="entry">
    <h1>Generar Nueva Entrada de Diarios</h1>
    <Button label="Generar Nueva Entrada" :handleClick="createNew"/>
  </div>
</template>
<script>
import Button from '@/shared-components/Button'
import config from '@/config'
import Client from '@/client'
export default {
  name: 'JournalEntry',
  components: {
    Button
  },
  methods: {
    async createNew () {
      try {
        let client = new Client({ uri: config.uri })
        let created = await client.saveNewEntry()
        console.log(created)
        alert('Nueva Entrada Creada Correcta')
      } catch (error) {
        alert('Ha ocurrido un error')
        console.log(error)
      }
    }
  }
}
</script>

<style scoped>
  .entry {
    display:flex;
    width: 100%;
    height:25em;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
</style>



